/* eslint-disable */

const funcNames = [
  "scanQrcode", "choosePhoto", "takePhoto", "accessLocation", "readContacts"
];
let exportObj = {}
funcNames.forEach(item => {
  exportObj[item] = function(args) {
    callHandler('ghaier_'+item, '', args);
  }
});
export default exportObj

function connectWebViewJavascriptBridge (callback) {
  if (window.WebViewJavascriptBridge) {
    callback(window.WebViewJavascriptBridge);
  } else {
    document.addEventListener(
      'WebViewJavascriptBridgeReady',
      function () {
        callback(window.WebViewJavascriptBridge);
      },
      false
    )
  }
}

function callHandler (code, handler_arg, args) {
  if(!window.WebViewJavascriptBridge) {
    console.error("ghaier: not found WebViewJavascriptBridge from window");
  }else {
    window.WebViewJavascriptBridge.callHandler(code, handler_arg, function (responseData) {
      if (responseData) {
        console.log(JSON.stringify(responseData));
        if (args.success) {
          typeof args.success == "function" & args.success(responseData);
        }
      } else {
        // fail
        if (args.fail) {
          typeof args.fail == "function" & args.fail(responseData);
        }
      }
      if (args.complete) {
        typeof args.complete == "function" & args.complete(responseData);
      }
    });
  }
}

if(window && !window.jsbridge_inited) {
  connectWebViewJavascriptBridge(function (bridge) {
    bridge.init(function (message, responseCallback) {
      //bridge初始化
    });
    bridge.registerHandler("JSKey", function (data, responseCallback) {
      //native通过"JSKey"关键字找到对应的handler
      //data是native传过来的数据
      //responseCallback 是native与js通信的回调，若responseCallback不为空，直接responseCallback(responseData)即可
      responseCallback("js responseCallback")
    });
  });
  window.jsbridge_inited = true;
}
